.add-product-title-container {
	width: 100%;
	height: 100%;
	background-color: #f8f8f8;
	grid-area: add-product-title-container;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

.add-product-title {
	width: 100%;
	grid-area: add-product-title;
	display: flex;
	align-self: center;
	justify-content: flex-start;
}

.add-product-title h2 {
	margin: 0;
	padding: 0;
}

.add-product-title {
	text-align: start;
}

.add-product-saveButton {
	width: 100%;
	justify-self: flex-end;
	justify-content: flex-end;
	align-self: center;
	display: flex;
	flex-direction: row;
	grid-area: add-product-saveButton;
}

.add-product-saveButton button {
	width: 150px;
	margin-left: 20px;
}

.add-product-info-container {
	width: 100%;
	height: 100%;
	background-color: #ffffff;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: 0.5fr repeat(2, 1fr);
	gap: 0px 0px;
	grid-template-areas:
		"addProduct-info-title addProduct-info-title addProduct-info-title"
		"addProduct-info-img addProduct-info-name addProduct-info-brand"
		"addProduct-info-img addProduct-info-id addProduct-info-category";
	grid-area: add-product-info-container;
	border-radius: 25px;
}

.addProduct-info-title {
	margin: 0% 4% 0% 4%;
	padding-top: 1.5%;
	padding-bottom: 1%;
	grid-area: addProduct-info-title;
	border-bottom: 1px solid black;
	margin-bottom: 2%;
}

.addProduct-info-title h2 {
	margin: 0;
	padding: 0;
}

.addProduct-info-img {
	display: grid;
	grid-template-columns: 0.1fr 1fr 0.1fr;
	grid-template-rows: 0.1fr 1fr 0.1fr;
	gap: 0px 0px;
	grid-template-areas:
		". . ."
		". addProduct-image-circle ."
		". . .";
	grid-area: addProduct-info-img;
}

.addProduct-image-circle {
	width: 50%;
	height: 75%;
	background-color: #fbae43;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr 0.7fr;
	gap: 0px 0px;
	grid-template-areas:
		"pictureIcon"
		"addProduct-semiCirlce-wrapper";
	grid-area: addProduct-image-circle;
	justify-content: center;
	justify-self: center;
	justify-items: center;
	align-items: center;
	align-self: center;
	align-content: center;
}

.pictureIcon img {
	width: 24px;
	height: 24px;
	margin-top: 150%;
}

.pictureIcon {
	grid-area: pictureIcon;
}

.addProduct-semiCirlce-wrapper {
	background-color: rgba(22, 22, 22, 0.6);
	grid-area: addProduct-semiCirlce-wrapper;
	border-bottom-left-radius: 50% 100%;
	border-bottom-right-radius: 50% 100%;
	width: 96%;
	height: 100%;
	display: flex;
	justify-content: center;
	justify-self: center;
	justify-items: center;
	align-items: center;
	align-self: center;
	align-content: center;
}

